body {
  margin: 0;
  padding: 0;
  background-color: rgb(0, 0, 0);
}
.break {
  position: fixed;
  background-color: rgb(0, 0, 0);
  height: 100vh;
  width: 100vw;
  animation: broken 2s linear;
  z-index: 999;
  font-size: .5rem;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.normal {
  display: none;
} 
.switch {
  margin-left: 1rem;
  transform: rotate(90deg);
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}
.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}
input:checked + .slider {
  background-color: #2196F3;
}
input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}
input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
@media (min-width: 700px) {
  .break {
    font-size: 1rem;
    }
}

